import React, {Component} from 'react';
import { CHANGE_TAB } from '../../constants/actionTypes';
import { connect } from 'react-redux';
import './style.css';

const TabItem = props => {
  const clickHandler = () => {
    props.onTabClick(props.matchText);
  }
  return (
    <span className={props.tab === props.matchText ? 'active' : ''}
          onClick={clickHandler}>
      {props.children}
    </span>
  );
}

const mapStateToProps = state => ({
  ...state.shoppingCart,
});

const mapDispatchToProps = dispatch => ({
  onTabClick: tab => dispatch({ type: CHANGE_TAB, tab})
});
class ShoppingCartTab extends Component {
  constructor(props) {
    super(props);
    this.state = {
      tableItems: [
        {
          matchText: 'one',
          displayName: '第一页',
        },
        {
          matchText: 'two',
          displayName: '第二页',
        }
      ]
    };
  }
  render() {
    return (
      <div className="switch-tab">
        {
          this.state.tableItems.map((item, index) => {
            return <TabItem key={index}
                            matchText={item.matchText}
                            tab={this.props.tab}
                            onTabClick={this.props.onTabClick}>
                     {item.displayName}
                   </TabItem>;
          })
        }
      </div>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(ShoppingCartTab);
