import React, {Component} from 'react';
import {NavLink} from 'react-router-dom';
import './style.css';

class BottomTab extends Component {
  constructor(props) {
    super(props);
    this.state = {
      tabInfo: [
        {
          name: "清单",
          path: "/shopping-cart",
          className: 'shopping-cart',
        },
        {
          name: "买书",
          path: "/goods-info/book",
          className: 'book',
        },
        {
          name: "买手机",
          path: "/goods-info/phone",
          className: 'phone',
        },
      ],
    }
  }

  render() {
    return (
      <ul style={styles.list}>
        {
          this.state.tabInfo.map((item, index) => {
            return (
              <li key={index} style={{flexGrow: 1}}>
                <NavLink activeClassName="active" style={styles.linkStyle} to={item.path}>
                  <div style={styles.logo} className={item.className}></div>
                  <span>{item.name}</span>
                </NavLink>
              </li>
            );
          })
        }
      </ul>
    );
  }
}

const styles = {
  linkStyle: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  list: {
    flexGrow: 0,
    display: 'flex',
    listStyle: 'none',
    width: '100%',
    padding: '10px 0',
    margin: '0',
    fontSize: '1.5em',
    justifyContent: 'space-around',
    order: 100,
    alignSelf: 'flex-end',
    borderTop: '1px solid',
  },
  logo: {
    width: '30px',
    height: '30px',
    backgroundRepeat: 'no-repeat',
    backgroundSize: '100% 100%',
  }
}
export default BottomTab;
