import {
  INIT_GOODS_INFO,
  ADD_NUM,
  DECREASE_NUM
} from '../constants/actionTypes';

export default (state = {}, action) => {
  switch(action.type) {
    case INIT_GOODS_INFO: return {
      ...state,
      items: action.items,
    };
    case ADD_NUM: return {
      ...state,
      items: state.items.map((item) => {
        if(item.goodsName === action.goodsName) {
          return {
            ...item,
            num: ++item.num,
          };
        }
        return item;
      })
    };
    case DECREASE_NUM: return {
      ...state,
      items: state.items.map((item) => {
        if(item.goodsName === action.goodsName) {
          if(item.num < 1) return item;
          return {
            ...item,
            num: --item.num,
          };
        }
        return item;
      })
    };
    default:
      return state;

  }
}
