import {
  CHANGE_TAB,
  INIT_CART_TAB
} from "../constants/actionTypes";

export default (state = {}, action) => {
  switch (action.type) {
    case CHANGE_TAB: return {
      ...state,
      tab: action.tab,
    };
    case INIT_CART_TAB: return {
      ...state,
      tab: action.tab,
    };
    default:
      return state;
  }
};
