import React, {Component} from 'react';
import { connect } from 'react-redux';
import {ADD_NUM, DECREASE_NUM} from "../../constants/actionTypes";
import './style.css';
const mapDispatchToProps = dispatch => ({
  addNum: goodsName => dispatch({ type: ADD_NUM, goodsName}),
  decreaseNum: goodsName => dispatch({ type: DECREASE_NUM, goodsName})
});
class ControlPanel extends Component {
  render() {
    const item = this.props.item;
    return (
      <div className="control-panel">
        <span onClick={() =>{this.props.decreaseNum(item.goodsName)}}>-</span>
        <span>{item.num}</span>
        <span onClick={() =>{this.props.addNum(item.goodsName)}}>+</span>
      </div>
    );
  }
}

export default connect(null, mapDispatchToProps)(ControlPanel);
