import React, {Component} from 'react';
import { connect } from 'react-redux';
import ShoppingCartTab from '../../components/ShoppingCartTab';
import ShoppingList from '../../components/ShoppingList';
import { INIT_CART_TAB } from '../../constants/actionTypes';
import './style.css';

const initTableText = 'one';

const mapStateToProps = state => ({
  ...state.shoppingCart,
});

const mapDispatchToProps = dispatch => ({
  onInit: tab => dispatch({ type: INIT_CART_TAB, tab})
});

class ShoppingCart extends Component {
  componentWillMount() {
    this.props.tab || this.props.onInit(initTableText);
  }
  render() {
    return (
      <div className="cart-wrapper">
        <ShoppingCartTab />
        <ShoppingList />
      </div>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(ShoppingCart);



