import React, {Component} from 'react';
import { connect } from 'react-redux';
import ControlPanel from '../ControlPanel'
import './style.css';
import {
  ADD_NUM,
  DECREASE_NUM
} from "../../constants/actionTypes";

const mapStateToProps = state => ({
  ...state.goodsInfo,
  ...state.shoppingCart,
});
const mapDispatchToProps = dispatch => ({
  addNum: goodsName => dispatch({ type: ADD_NUM, goodsName}),
  decreaseNum: goodsName => dispatch({ type: DECREASE_NUM, goodsName})
});
class ShoppingList extends Component {
  componentWillMount() {
    console.log(this.props.items);
  }
  render() {
    let bucketNum = 0;
    switch (this.props.tab) {
      case 'one': break;
      case 'two':
        bucketNum = 1 ;
        break;
      default: break;
    }
    const item = this.props.items.find(item => {
      return item.bucketNum === bucketNum;
    });
    return (
      <div className="list-wrapper">
        <ul style={{padding: '0px 20px'}}>
          <li className="list-item">
            <div className="item-info">
              <span style={{backgroundImage: `url(${item.imgPath})`}}></span>
              <span>{item.nickName}</span>
            </div>
            <ControlPanel item={item}/>
          </li>
        </ul>
      </div>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(ShoppingList);
