import React, {Component} from 'react';
import {Route, Switch} from 'react-router-dom';
import {connect} from 'react-redux';
import BottomTab from '../components/BottomTab';
import GoodsInfo from './GoodsInfo';
import ShoppingCart from './ShoppingCart';
import {INIT_GOODS_INFO} from "../constants/actionTypes";

const mapStateToProps = state => ({
  ...state.goodsInfo,
});
const mapDispatchToProps = dispatch => ({
  onInit: items => dispatch({type: INIT_GOODS_INFO, items})
});

class App extends Component {
  componentWillMount() {
    console.log(this.props.items);
    this.props.items || this.props.onInit([
        {
          goodsName: 'book',
          nickName: '书',
          imgPath: '/icon/book.svg',
          num: 0,
          bucketNum: 0,
        },
        {
          goodsName: 'phone',
          nickName: '手机',
          imgPath: '/icon/mobile.svg',
          num: 0,
          bucketNum: 1,
        },
      ]
    );
  }

  componentWillUnmount() {

  }

  render() {
    return (
      <div style={styles.main}>
        <div style={styles.cardStyle}>
          <Switch>
            <Route exact path="/" component={ShoppingCart}/>
            <Route exact path="/shopping-cart" component={ShoppingCart}/>
            <Route exact path="/goods-info/:goodsName" component={GoodsInfo}/>
            <Route exact path="/goods-info/:goodsName" component={GoodsInfo}/>
          </Switch>
        </div>
        <BottomTab/>
      </div>
    );
  }
}

const styles = {
  main: {
    display: 'flex',
    flexWrap: 'wrap',
    height: '100%',
    flexDirection: 'column',
  },
  cardStyle: {
    flexGrow: 1,
  },
}
export default connect(mapStateToProps, mapDispatchToProps)(App);
