import React, {Component} from 'react';
import ControlPanel from '../../components/ControlPanel';
import { connect } from 'react-redux';
import './style.css';

const mapStateToProps = state => ({
  ...state.goodsInfo,
});

class GoodsInfo extends Component {

  render() {
    const goodsName = this.props.match.params.goodsName;
    let item = this.props.items.find(item => {
      return item.goodsName === goodsName
    });
    if (!item) {
      item = this.props.items[0];
    }
      return (
        <div className="goods-info-wrapper">
          <div className="goods-info">{item.nickName}</div>
          <ControlPanel item={item}/>
        </div>
      );
  }
}
export default connect(mapStateToProps)(GoodsInfo);
